<template>
  <div class="playlist">
    <HomeLink></HomeLink>
    <searchInput @s-input="getSuggest" @s-focus="showOrHide=true"></searchInput>
    <hr />
    <div class="hotSearch" v-if="showOrHide">
      <h3 class="title">热门搜索</h3>
      <ul class="list">
        <hotKeyWord
          v-for="(item, index) in HotSearch"
          :key="index"
          :hotword="item.first"
          @search="getSearch"
        ></hotKeyWord>
      </ul>
    </div>
    <template v-if="!showOrHide">
      <h3 class="pipei">最佳匹配</h3>
      <searchContent
        v-for="(item, index) in searchSongs"
        :key="index"
        :songItem="item"
        @searcard-list="$emit('searcard-list', $event)"
        :currentSong="currentSong"
      ></searchContent>
    </template>
    <template v-if="showOrHide">
      <h3 class="inputValue" @click="getSearch(inputValue)">{{inputValue}}</h3>
      <searchSuggestList
        v-for="(item, index) in searchSuggest"
        :key="index+'-only'"
        :suggestWord="item.keyword"
        @search-content="getSearch"
      ></searchSuggestList>
    </template>
  </div>
</template>

<script>
import HomeLink from "@/components/HomeLink.vue";
import searchInput from "@/components/searchInput.vue";
import searchContent from "@/components/searchContent.vue";
import hotKeyWord from "@/components/hotKeyWord.vue";
import searchSuggestList from "@/components/searchSuggestList.vue";
export default {
  props: {
    currentSong: Object
  },
  components: {
    HomeLink,
    searchInput,
    searchContent,
    hotKeyWord,
    searchSuggestList
  },
  data() {
    return {
      searchSongs: null,
      HotSearch: null,
      searchSuggest: null,
      inputValue: "",
      showOrHide: true
    };
  },
  methods: {
    getSearch: function(v) {
      this.showOrHide = false;
      // console.log(v)
      if (!v) {
        this.searchSongs = [];
        return;
      }
      this.axios
        .get("/search", {
          params: {
            // type:'mobile',
            keywords: v
          }
        })
        .then(response => {
          console.log(response);
          this.searchSongs = response.data.result.songs;
          // console.log(this.searchSongs);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getSuggest: function(v) {
      if (v) {
        this.inputValue = "搜索" + '"' + v + '"';
      } else {
        this.inputValue = "";
      }

      // console.log(v);
      if (!v) {
        this.searchSuggest = [];
        return;
      }
      this.axios
        .get("/search/suggest", {
          params: {
            type: "mobile",
            keywords: v
          }
        })
        .then(response => {
          console.log(response);
          this.searchSuggest = response.data.result.allMatch;
          // console.log(this.searchSongs);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created: function() {
    console.log(this);
    this.axios
      .get("/search/hot")
      .then(response => {
        // console.log(response);
        this.HotSearch = response.data.result.hots;
        // console.log(this.HotSearch);
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>
<style lang="less" scoped>
.playlist{
  padding-top: 40px;
}
hr {
  border: 0;
  border-bottom: 1px solid lightgray;
}
.hotSearch {
  padding: 15px 10px 0;
  .title {
    font-size: 12px;
    line-height: 12px;
    color: #666;
  }
  .list {
    margin: 10px 0 7px;
  }
}
.inputValue {
  height: 50px;
  margin-left: 10px;
  padding-right: 10px;
  font-size: 15px;
  line-height: 50px;
  color: #507daf;
}
.pipei {
  padding: 8px 0 4px;
  margin-left: 10px;
  font-size: 12px;
  line-height: 16px;
  color: #666;
}
</style>
