<template>
  <div class="playlist">
    <HomeLink></HomeLink>
    <!-- <h1>http://music.kele8.cn/top/list?idx=1</h1> -->
    <div class="hearderTag">
      <div class="hotBgTitle"></div>
    </div>
    <template v-if="HotSongList">
      <HotSong
        v-for="(item, index) in getPersonalizedLimit()"
        :key="index"
        :songItem="item"
        @songcard-list="$emit('songcard-list', $event)"
        :currentSong="currentSong"
      ></HotSong>
    </template>
  </div>
</template>
<script>
import HomeLink from "@/components/HomeLink.vue";
import HotSong from "@/components/HotSong.vue";
export default {
   props: {
    currentSong: Object
  },
  components: {
    HomeLink,
    HotSong
  },
  data() {
    return {
      HotSongList: null
    };
  },
  methods: {
    getHotSongList: function() {
      this.axios
        .get("/top/list?idx=1")
        .then(response => {
          console.log("热歌榜", response);
          this.HotSongList = response.data.playlist.tracks;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getPersonalizedLimit: function() {
      return this.HotSongList.slice(0, 20);
    }
  },
  created() {
    this.getHotSongList();
  }
};
</script>
<style lang="less" scoped>
  .playlist{
    padding-top: 40px;
  }
  .hearderTag {
    display: flex;
    height: 145px;
    // position: absolute;
    // left: 0;
    // right: 0;
    // top: 0;
    // bottom: 0;
    background: url(../assets/hot_music_bg.jpg) no-repeat;
    z-index: 2;
    padding: 20px;
    box-sizing: border-box;
    .hotBgTitle{
      width: 200px;
      background: url(../assets/index_icon_2x.png) no-repeat;
     background-size: 155px 97px;
    background-position: -10px -23px;
    }
}
</style>