<template>
  <div class="playbar">
    <!-- bottom -->
    <div class="playbarbottom" @click="showOrhide">
      <img
        v-if="flag === 'a'"
        class="thumb"
        :class="{active: playing}"
        :src="currentSong.al.picUrl"
        alt
      />
      <img
        v-else-if="flag === 'b'"
        class="thumb"
        :class="{active: playing}"
        :src="currentSong.picUrl"
        alt
      />
      <img
        v-else-if="flag === 'c'"
        class="thumb"
        :class="{active: playing}"
        src="https://p2.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg"
        alt
      />
      <div class="info">
        <h5>{{currentSong.name}}</h5>
        <span v-if="flag === 'a'">{{currentSong.ar[0].name}}</span>
        <span v-else-if="flag === 'b'">{{joinArtists(currentSong.song.artists)}}</span>
        <span v-else-if="flag === 'c'">{{currentSong.artists[0].name}}</span>
      </div>
      <div class="progress" @click.stop="playing = !playing">
        <canvas width="36" height="36" id="canvas"></canvas>
        <i v-if="playing" class="fa fa-pause"></i>
        <i v-else class="fa fa-play"></i>
      </div>
      <div class="playlist" @click.stop="showPlayList=!showPlayList">
        <i class="fa fa-bars" aria-hidden="true"></i>
      </div>
    </div>

    <!-- 播放列表 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutDown"
    >
      <ul v-if="showPlayList" class="defaultPlayList">
        <SongItem
          v-for="(item, index) in defaultPlayList"
          :key="index"
          :songItem="item"
          :options="{order: index, info: false}"
          @tanslate-song="Submit"
          :currentSong="currentSong"
        ></SongItem>
      </ul>
    </transition>

    <!-- fullscreen -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
    >
      <div v-show="showFullscreen" class="fullscreenplay">
        <div
          v-if="flag === 'a'"
          class="mask"
          :style="{backgroundImage: `url(${currentSong.al.picUrl})`}"
        ></div>
        <div
          v-else-if="flag === 'b'"
          class="mask"
          :style="{backgroundImage: `url(${currentSong.picUrl})`}"
        ></div>
        <div
          v-else-if="flag === 'c'"
          class="mask"
          :style="{backgroundImage: `url(https://p2.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg)`}"
        ></div>

        <header>
          <img src="../assets/arrow_left.png" @click="showAndhide" />
          <div class="songDetail">
            <div class="songName">{{currentSong.name}}</div>
            <div v-if="flag === 'a'" class="songerName">{{currentSong.ar[0].name}}</div>
            <div
              v-else-if="flag === 'b'"
              class="songerName"
            >{{joinArtists(currentSong.song.artists)}}</div>
            <div v-else-if="flag === 'c'" class="songerName">{{currentSong.artists[0].name}}</div>
          </div>
        </header>

        <div
          v-show="showCircle"
          class="circle"
          :class="{active: playing}"
          @click="showCircle = false"
        >
          <div class="needle"></div>
          <div class="glue">
            <img v-if="flag === 'a'" :src="currentSong.al.picUrl" alt />
            <img v-else-if="flag === 'b'" :src="currentSong.picUrl" alt />
            <img
              v-else-if="flag === 'c'"
              src="https://p2.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg"
              alt
            />
          </div>
        </div>
        <div v-show="!showCircle" class="lyric-box" @click="showCircle = true">
          <ul class="scroll" :style="{transform: `translateY(${-currentLyricIndex * 28}px)`}">
            <li
              v-for="(item, index) in parsedLyric"
              :class="{active: index===currentLyricIndex}"
              :key="index"
            >{{item.text}}</li>
          </ul>
        </div>

        <transition name="custom-classes-transition">
          <ul v-if="showfullScreenList" class="fullScreenPlayList">
            <SongItem
              v-for="(item, index) in defaultPlayList"
              :key="index"
              :songItem="item"
              :options="{order: index, info: false}"
              @tanslate-song="Submit"
              :currentSong="currentSong"
              @stop-Toggle="stopToggle()"
            ></SongItem>
          </ul>
        </transition>

        <footer>
          <div class="progressBox">
            <div class="currentTime">{{currentTime}}</div>
            <div class="progressBar" ref="abc" @click="mousePosition">
              <div class="bgProgress"></div>
              <div class="progressBlock"></div>
            </div>
            <div class="duration">{{duration}}</div>
          </div>
          <div class="controlPlay">
            <div class="palyOrder" @click="cut">
              <img :src="cutPlayOrder" />
            </div>
            <div class="presong" @click="TogglePre">
              <img src="../assets/prev.png" />
            </div>
            <div class="play-pause" @click.stop="playing = !playing">
              <img v-if="playing" src="../assets/pause.png" />
              <img v-else src="../assets/play.png" />
            </div>
            <div class="nextsong">
              <img src="../assets/next.png" @click="ToggleNext" />
            </div>
            <div class="palyList" @click.stop="showfullScreenList=!showfullScreenList">
              <img src="../assets/menu.png" />
            </div>
          </div>
        </footer>
      </div>
    </transition>

    <audio :src="currentSongUrl" autoplay height="30"></audio>
  </div>
</template>

<script>
var progressBar;
var progressBlock;
var bgProgress;
var maxLeft;
var audio;
var duration;

import SongItem from "@/components/SongItem";
import danqu from "../assets/danqu_32.png";
import list from "../assets/liebiao.png";
import random from "../assets/random.png";
import Vue from "vue";
export default {
  props: {
    currentSong: Object,
    flag: String
  },
  components: {
    SongItem
  },
  data: function() {
    return {
      playing: false,
      showFullscreen: false,
      showCircle: false,
      currentLyric: null,
      currentLyricIndex: 0,
      showPlayList: false,
      showfullScreenList: false,
      defaultPlayList: [this.currentSong],
      cutOrder: [danqu, list, random],
      cutPlayOrder: danqu,
      i: 1,
      currentTime: "",
      duration: ""
    };
  },
  methods: {
    joinArtists: function(artists) {
      return artists.map(artist => artist.name).join(" / ");
    },
    drawProgress: function() {
      var canvas = this.$el.querySelector("#canvas");
      var audio = this.$el.querySelector("audio");
      var duration;
      if (this.currentSong.song) {
        duration = this.currentSong.song.duration;
      } else {
        duration = this.currentSong.dt;
      }

      console.log(duration);
      audio.ontimeupdate = () => {
        // 当进入播放页面时
        if (this.showFullscreen) {
          console.log('-----')
          // 触发进度条功能
          setTimeout(() => {
            this.getProgressLeft();
          }, 0);
          
        }

        // console.log(duration);
        // 获取canvas元素
        var context = canvas.getContext("2d");
        context.clearRect(0, 0, 36, 36);

        context.beginPath();
        context.arc(18, 18, 17, 0, 2 * Math.PI);
        context.closePath();
        context.strokeStyle = "gray";
        // 描边样式
        context.lineWidth = "1";
        // 描边宽度
        context.stroke(); // 路径描边

        context.beginPath();
        context.arc(
          18,
          18,
          17,
          -0.5 * Math.PI,
          -0.5 * Math.PI + 2 * Math.PI * ((audio.currentTime * 1000) / duration)
        );
        // context.closePath();
        context.strokeStyle = "rgba(255,0,0,0.7)";
        // 描边样式
        context.lineWidth = "1";
        // 描边宽度
        context.stroke(); // 路径描边

        // 滚动歌词
        // this.currentTime
        // currentLyricIndex
        let index;

        // 每当播放时间发生改变就执行一次for
        for (let i = 0; i < this.parsedLyric.length; i++) {
          // 找到当前播放时间与歌词时间对应的那句歌词
          if (audio.currentTime + 0.15 < this.parsedLyric[i].time) {
            // 记录该句歌词所在的下标
            index = i - 1;
            break;
          }
        }
        if (index === undefined) {
          index = this.parsedLyric.length - 1;
        }
        // 将下标值赋给变量，然后乘以歌词行高再加个负号作为歌词框的top值
        this.currentLyricIndex = index;
      };
    },
    getLyric: function() {
      // 获取缓存中存放的歌词数据
      var cachedLyric = window.localStorage.getItem(
        "lyric" + this.currentSong.id
      );

      if (cachedLyric) {
        this.currentLyric = cachedLyric;
      } else {
        // 如果缓存没有数据则请求获取
        window.axios
          .get("lyric", {
            params: {
              id: this.currentSong.id
            }
          })
          .then(response => {
            this.currentLyric = response.data.lrc.lyric;

            window.localStorage.setItem(
              "lyric" + this.currentSong.id,
              response.data.lrc.lyric
            );
          });
      }
    },

    showOrhide: function() {
      this.showFullscreen = true;
      this.showPlayList = false;

      //因为弹性布局的原因，在获取获取进度条元素宽度前先获取当前时间和总时间
      this.currentTime = this.formatTime(audio.currentTime);
      this.duration = this.formatTime(audio.duration);
  
      // 这里用延时器是因为隐藏的dom元素未完全被渲染，获取不了正确的offsetWidth
      setTimeout(() => {
        // 获取进度条
        progressBar = this.$el.querySelector(".progressBar");
        // 获取移动块
        progressBlock = this.$el.querySelector(".progressBlock");
        // 获取背景层
        bgProgress = this.$el.querySelector(".bgProgress");
        // 进度条宽度-移动块宽度=最大可移动宽度
        maxLeft = progressBar.offsetWidth - progressBlock.offsetWidth;
        duration = audio.duration;
        // 时间格式化
        // this.duration = this.formatTime(duration);
        console.log(duration)
        // 之所以在这里执行一次该函数，是防止出现用户在播放页外面暂停再点击进来播放页面时出现的currentTime和背景层都没有被渲染的问题。
        this.getProgressLeft();
      }, 0);
    },

    // 播放进度条功能
    getProgressLeft: function() {
      var currentTime = audio.currentTime;
      this.currentTime = this.formatTime(currentTime);
      //  console.log("当前时间"+currentTime)
      //获取进度百分比
      var percent = currentTime / duration;
      // console.log("百分比"+percent)
      // 最长可移动距离*百分比=每步移动距离
      var left = maxLeft * percent + "px";
      // console.log("移动距离"+maxLeft)
      // left/maxLeft*duration=currentTime
      // 滑块随着left移动
      progressBlock.style.left = left;
      // 改变激活层宽度
      bgProgress.style.width = left;
    },
    showAndhide: function() {
      this.showFullscreen = false;
      this.showfullScreenList = false;
    },
    cut: function() {
      if (this.i < 3) {
        this.cutPlayOrder = this.cutOrder[this.i];
        this.i++;
      } else {
        this.cutPlayOrder = this.cutOrder[0];
        this.i = 1;
      }
    },

    // 切换到上一曲
    TogglePre: function() {
      for (var i = 0; i < this.defaultPlayList.length; i++) {
        // 获取当前歌曲的下标
        var index = this.defaultPlayList.indexOf(this.currentSong);

        // 使用Vue.set是因为vue视图更新不会及时检测到数组属性的变化
        // this.defaultPlayList[i].cut=false
        Vue.set(this.defaultPlayList[i], "cut", false);
      }
      // 当前歌曲为第一首时，切换上一曲无效
      if (index - 1 < 0) {
        return;
      }
      // 将pre属性赋给当前歌曲的上一曲，这是上曲切换的关键
      // this.defaultPlayList[index-1].cut=true
      Vue.set(this.defaultPlayList[index - 1], "cut", true);
    },

    // 切换到下一曲
    ToggleNext: function() {
      for (var i = 0; i < this.defaultPlayList.length; i++) {
        var index = this.defaultPlayList.indexOf(this.currentSong);

        Vue.set(this.defaultPlayList[i], "cut", false);
      }
      // 当前歌曲为最后一首时，切换下一曲无效
      if (index + 1 > this.defaultPlayList.length - 1) {
        return;
      }
      Vue.set(this.defaultPlayList[index + 1], "cut", true);
    },
    // 此函数防止点击上下曲切换后不能正常点击任意歌曲的问题出现 注意要引入Vue
    stopToggle: function() {
      for (var i = 0; i < this.defaultPlayList.length; i++) {
        // 当点击任意歌曲时，将列表的所有歌曲的cut属性变为false
        //  this.defaultPlayList[i].cut=false
        Vue.set(this.defaultPlayList[i], "cut", false);
      }
    },

    // 格式化时间
    formatTime(sec) {
      //sec: 秒数
      //不足10，需要补零
      var minute = Math.floor(sec / 60);
      minute = minute >= 10 ? minute : "0" + minute;

      var second = Math.floor(sec % 60);

      second = second >= 10 ? second : "0" + second;

      return minute + ":" + second;
    },

    // 判断歌曲属于那种数据结构，然后触发父组件中对应的自定义事件
    Submit($event) {
      // console.log($event)

      // 这里要用$event而不要用this.currentSong,因为$evnet是实时上传的歌曲数据，而后者并不是
      if ($event.ar) {
        // $event.ar  热歌榜和歌单的数据结构
        this.$emit("songcard-list", $event);
      } else if ($event.picUrl) {
        //$event.picUrl  首页的数据结构
        this.$emit("tanslate-song", $event);
      } else {
        this.$emit("searcard-list", $event);
      }
    },
    mousePosition(){
      // console.log(event)
      var offsetLeft=this.$el.querySelector(".progressBar").offsetLeft
      var Left=event.clientX-offsetLeft
      audio.currentTime=(Left/maxLeft)*duration
      // currentTime=left/maxLeft*duration
      // console.log(ow)
      console.log(audio.currentTime)
    }
  },

  computed: {
    currentSongUrl: function() {
      if (this.currentSong)
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      else return "";
    },
    // 整理歌词格式
    parsedLyric: function() {
      if (this.currentLyric) {
        // 先以换行符将歌词切割成数组，再用map循环数组每一项
        return this.currentLyric.split("\n").map(item => {
          // console.log(item);
          // 正则匹配时间格式
          var regex = /\d{2}:\d{2}\.\d+/i;
          // search方法返回相匹配的子串的起始位置，否则返回-1
          if (item.search(regex) !== -1) {
            // match方法返回匹配结果的数组，其中下标0才是匹配到的文本
            var time = item.match(regex)[0];
            var m = time.substr(0, 2);
            var s = time.substr(3, 2);
            var n = time.substr(5);
            return {
              // 将时间转换为秒单位
              time: Number(m) * 60 + Number(s) + Number(n),
              text: item.substr(11) || "---------"
            };
          } else {
            return {};
          }
        });
      } else {
        return null;
      }
    }
  },

  mounted() {
    audio = this.$el.querySelector("audio");
    // progressBar = this.$el.querySelector(".progressBar");

    this.drawProgress();
    // audio.play
    audio.onplay = () => {
      this.playing = true;
    };
    audio.onpause = () => {
      this.playing = false;
    };
  },

  watch: {
    playing: function(value) {
      var audio = this.$el.querySelector("audio");
      if (value) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    // 监听歌曲数据变化
    currentSong: function(value) {
      this.getLyric();
      // 加入列表 需要过滤重复
      // this.defaultPlayList.push(value);

      // 利用数组some方法，遍历defaultPlayList里的每个元素
      let isExist = this.defaultPlayList.some(item => {
        // 判断歌曲是否重复
        return item.id == value.id;
      });

      console.log(isExist);
      // 如果此歌曲与列表中的歌曲没有重复的话，将此歌曲放进列表
      if (!isExist) this.defaultPlayList.push(value);
    }
  },
  created() {
    this.getLyric();
  }
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbarbottom {
  padding: 5px 0;
  background: white;
  border-top: 1px solid rgba(128, 128, 128, 0.2);
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  .margin () {
    margin: 0 10px;
  }
  .thumb {
    .margin;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    animation: rotate 6s linear infinite;
    animation-play-state: paused;
    &.active {
      animation-play-state: running;
    }
  }
  .info {
    flex: 1;
    font-size: 12px;
    line-height: 18px;
    span {
      color: gray;
    }
  }
  .progress {
    .margin;
    width: 28px;
    height: 28px;
    // background: lightgray;
    position: relative;
    #canvas {
      width: 100%;
    }
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      color: lightgray;
      font-size: 12px;
      display: block;
      margin-top: -6px;
      margin-left: -6px;
      line-height: 1em;
      width: 12px;
      height: 12px;
      text-align: center;
    }
  }
  .playlist {
    .margin;
    width: 28px;
    height: 28px;
    // background: red;
    position: relative;
    border: 1px solid lightgray;
    border-radius: 50%;
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      color: lightgray;
      font-size: 12px;
      display: block;
      margin-top: -6px;
      margin-left: -6px;
      line-height: 1em;
      width: 12px;
      height: 12px;
      text-align: center;
    }
  }
}

.defaultPlayList {
  position: fixed;
  bottom: 50px;
  left: 2.5%;
  width: 95%;

  background: white;
  border-radius: 15px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
  z-index: 9;
}
.fullScreenPlayList {
  position: fixed;
  bottom: 111px;
  left: 2.5%;
  width: 95%;

  background: white;
  border-radius: 15px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
  z-index: 9;
}
.fullscreenplay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  .mask {
    filter: blur(30px) brightness(0.5);
    transform: scale(2);
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    z-index: -1;
  }
  header {
    display: flex;
    align-items: center;
    height: 80px;
    padding: 10px 0;
    color: white;
    .songDetail {
      margin-left: 10px;
      .songerName {
        font-size: 14px;
        margin-top: 3px;
      }
    }
  }
  .circle {
    flex: 1;
    text-align: center;
    position: relative;
    .glue {
      padding: 54px;
      // background: red;
      background-image: url("../assets/disc_light-ip6.png"),
        url("../assets/disc-ip6.png");
      background-size: 100%;
      border-radius: 50%;
      display: inline-block;
      margin-top: 84px;
      animation: rotate 6s linear infinite;
      animation-play-state: paused;

      img {
        width: 180px;
        height: 180px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    .needle {
      width: 96px;
      height: 137px;
      background-image: url("../assets/needle.png");
      // background-color: lightblue;
      background-size: 100%;
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 1;
      margin-left: -16px;
      transform: rotate(-12deg);
      transform-origin: 16px 0;
      transition: all 0.3s;
    }
    &.active {
      .needle {
        transform: rotate(0deg);
      }
      .glue {
        animation-play-state: running;
      }
    }
  }
  .lyric-box {
    flex: 1;
    overflow: hidden;
    position: relative;

    .scroll {
      text-align: center;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;

      margin-top: -1em;
      transition: transform 0.3s;
      li {
        line-height: 2em;
        font-size: 14px;
        height: 2em;
        color: gray;
        &.active {
          color: white;
        }
      }
    }
  }
  footer {
    height: 100px;
    // background: lightblue;
    .progressBox {
      display: flex;
      align-items: center;
      height: 30px;
      color: white;
      // background-color: blue;
      // .currentTime
      .progressBar {
        flex: 1;
        height: 6px;
        background-color: black;
        position: relative;
        position: relative;
        .progressBlock {
          width: 10px;
          height: 10px;
          position: absolute;
          top: -2px;
          border-radius: 50%;
          background: white;
        }
        .bgProgress {
          width: 0;
          height: 100%;
          background: skyblue;
        }
      }
      .currentTime {
        margin: 0 5px;
      }
      .duration {
        margin: 0 5px;
      }
    }
    .controlPlay {
      height: 70px;
      // background-color: red;
      display: flex;
      align-items: center;
      div {
        flex: 1;
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        img {
          width: 32px;
          height: 32px;
        }
        &.play-pause {
          img {
            width: 52px;
            height: 52px;
          }
        }

